module.exports = "/*-------------------------------------------------*/\n/* Color HEX Codes */\n/*-------------------------------------------------*/\n/* orange-text: #FC7D14*/\n/* green-hero: #00796B */\n/* green-text: #00A58F */\n/* table-background-1: #111923 */\n/* table-background-2: #0D151F */\n/*-------------------------------------------------*/\n/* Basic Setup */\n/*-------------------------------------------------*/\nhtml, body {\n  font-size: 20px;\n  line-height: 1.5;\n  height: 100%;\n  background: #111923;\n  color: white;\n  font-family: 'Lato', 'Arial', sans-serif;\n  font-weight: 300;\n  text-rendering: optimizeLegibility;\n}\n.clearfix {zoom: 1;}\n.clearfix:after {\n    content: '.';\n    clear: both;\n    display: block;\n    height: 0;\n    visibility: hidden;\n}\n/*-------------------------------------------------*/\n/* Reusable Components */\n/*-------------------------------------------------*/\nnav {\n  height: 10vh;\n  background: #111923;\n}\nsection {\n  padding: 20px 0;\n  margin-bottom: 50px;\n}\n.row {\n  max-width: 90%; /*1140px or in %*/\n  margin: 0 auto;\n}\n.box {\n  padding: 1%;\n  /* box-shadow: 2px 2px 3px; */\n}\n.box-key {\n  text-align: left;\n  margin-left: 5px;\n}\n.box-value {\n  text-align: right;\n  margin-right: 5px;\n}\n.hero {\n  background: rgba(0, 121, 107, 0.8); /*#2461acb0*/\n}\n/*-------------------------------------------------*/\n/* Heading */\n/*-------------------------------------------------*/\nh1,\nh2,\nh3 {\n    font-weight: 300;\n    /* text-transform: uppercase; */\n}\nh1 {\n  margin-top: 0;\n  margin-bottom: 20px;\n  color: #ffffff;\n  font-size: 200%;\n  /* word-spacing: 3px;\n  letter-spacing: 1px; */\n}\nh2 {\n  font-size: 130%;\n  word-spacing: 2px;\n  text-align: left;\n  margin-bottom: 30px;\n  letter-spacing: 1px;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\nh3 {\n  font-size: 120%;\n  /* margin-bottom: 15px; */\n  text-align: center;\n}\n/*-------------------------------------------------*/\n/* Paragraphs */\n/*-------------------------------------------------*/\np {\n  font-size: 100%;\n  text-align: center;\n  line-height: 145%;\n  width: 70%;\n  margin-left: 15%;\n}\n/*-------------------------------------------------*/\n/* Links */\n/*-------------------------------------------------*/\na:link,\na:visited {\n    color: white;\n    text-decoration: none;\n    /* padding-bottom: 10px; */\n    /* border-bottom: 1px solid #e67e22; */\n    transition: color 0.2s;\n}\na:hover,\na:active {\n    color: #FC7D14;\n    /* border-bottom: 1px solid transparent; */\n}\n/*-------------------------------------------------*/\n/* Header */\n/*-------------------------------------------------*/\nheader {\n  height: 15vh;\n  background-size: cover;\n  background-position: center;\n  background-attachment: fixed;\n}\n.logo {\n  height: 100px;\n  width: auto;\n  float: left;\n  margin-top: 20px;\n}\n.main-nav {\n  float: right;\n  list-style: none;\n  margin-top: 55px;\n  display: flex;\n  list-style: none;\n  height: 100%;\n  align-items: center;\n  margin-left: auto;\n  justify-content: space-around;\n}\n.main-nav li {\n  display: inline-block;\n  margin-left: 40px;\n}\n.landing {\n  /* height: 90vh; */\n  /* display: flex; */\n  justify-content: center;\n  align-items: center;\n\n}\n/*-------------------------------------------------*/\n/* Specific */\n/*-------------------------------------------------*/\n/* .header-row,\n.proposer-row,\n.chain-queries-row {\n  max-width: 1140px;\n  margin: 0 20px;\n\n}  */\n.main-container {\n  min-height: 76vh;\n}\n.card {\n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);\n  margin-bottom: 2rem;\n}\n.card-header-title {\n  color:#8F99A3;\n  font-weight: 400;\n}\n.box {\n  background: #00796B;\n}\narticle {\n  background: #111923;\n}\n.title {\n  font-weight: 500;\n  color: rgb(255, 255, 255);\n}\n.subtitle {\n  font-weight: 400;\n  color:#111923;\n}\n.header-title,\n.header-subtitle {\n  text-align: left;\n}\n.header-subtitle {\n color: #FC7D14\n}\n.card .content {\n  font-size: 14px;\n}\n.card-footer-item {\n  font-size: 14px;\n  font-weight: 700;\n  color: #8F99A3;\n}\n.card-table .table {\n  margin-bottom: 0;\n}\n.events-card .card-table {\n  /*max-height: 500px;*/\n  overflow-y: scroll;\n}\n.navbar{\n  background: #0D151F;\n}\n/*-------------------------------------------------*/\n/* Footer */\n/*-------------------------------------------------*/\nfooter {\n  background-color: #0D151F;\n  color: white;\n  padding: 30px;\n}\n.footer-nav {\n  list-style: none;\n  float: left;\n  /* margin-top: 20px; */\n}\n.social-links {\n  list-style: none;\n  float: right;\n}\n.footer-nav li,\n.social-links li {\n    display: inline-block;\n    margin-right: 20px;\n}\n.footer-nav li:last-child,\n.social-links li:last-child {\n    margin-right: 0;\n}\n.footer-nav li:last-child,\n.social-links li:last-child {\n    margin-right: 0;\n}\nfooter p a:link,\nfooter p a:visited,\n.footer-nav li a:link,\n.footer-nav li a:visited,\n.social-links li a:link, \n.social-links li a:visited {\n    text-decoration: none;\n    border: 0;\n    color: #888888;\n    transition: color 0.2s;\n}\nfooter p a:hover,\nfooter p a:active,\n.footer-nav li a:hover,\n.footer-nav li a:active {\n    color: #FC7D14;\n}\n.social-links li a:link,\n.social-links li a:visited {\n    font-size: 150%;\n}\n.ion-social-facebook,\n.ion-social-twitter,\n.ion-social-googleplus,\n.ion-social-instagram {\n    transition: color 0.2s;\n}\n.ion-social-github:hover {\n    color: #a6b1b7;\n}\n.ion-social-twitter:hover {\n    color: #00aced;\n}\n.ion-paper-airplane:hover {\n    color: #0088cc;\n}\n.ion-social-linkedin:hover {\n    color: #007bb5;\n}\nfooter p {\n  color: #888888;\n  text-align: center;\n  margin-top: 20px;\n}\n/*-------------------------------------------------*/\n/* Responsive Nav */\n/*-------------------------------------------------*/\n@media screen and (max-width: 768px) {\n  .row {\n    max-width: 100%;\n  }\n\n  .line {\n    width: 30px;\n    height: 3px;\n    background: white;\n    margin: 5px;\n  }\n  \n  nav {\n    position: relative;\n  }\n\n  .hamburger {\n    margin-top: 40px;\n    position: absolute;\n    cursor: pointer;\n    right: 5%;\n    top: 50%;\n    -webkit-transform: translate(-5%, -50%);\n            transform: translate(-5%, -50%);\n    z-index: 2;\n  }\n\n  .main-nav {\n    position: fixed;\n    background: #111923;\n    height: 100vh;\n    width: 100%;\n    flex-direction: column;\n    clip-path: circle(100px at 90% -13%);\n    -webkit-clip-path: circle(100px at 90% -13%);\n    transition: all 0.6s ease-out;\n    pointer-events: none;\n    margin-top: 0;\n  }\n  .main-nav.open {\n    clip-path: circle(1050px at 90% -13%);\n    -webkit-clip-path: circle(1050px at 90% -13%);\n    pointer-events: all;\n  }\n  .landing {\n    flex-direction: column;\n  }\n  .main-nav li {\n    opacity: 1;\n  }\n  .main-nav li a {\n    font-size: 25px;\n  }\n  .main-nav li:nth-child(1) {\n    transition: all 0.5s ease 0.2s;\n  }\n  .main-nav li:nth-child(2) {\n    transition: all 0.5s ease 0.4s;\n  }\n  .main-nav li:nth-child(3) {\n    transition: all 0.5s ease 0.6s;\n  }\n  li.fade {\n    opacity: 1;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"